@import url(//fonts.googleapis.com/css?family=Actor);
.col2{background-color:black;display:flex}.title--big{color:#e50914}.nav__link{color:#e50914}.nav__item{list-style:none}

.card{display:flex;flex-direction:row;flex-wrap:wrap;list-style:none;justify-content:space-between;margin-right:0.5rem;margin-bottom:0.5rem;max-width:7.5rem;border-radius:0.5rem;background-color:white;border:0.1875rem solid #e50914;overflow:hidden;transition:0.25s all;background-color:black;color:#cecece}.card__img{width:100%}.cards{display:flex;flex-direction:row;flex-wrap:wrap;list-style:none}

.movies__filters{display:flex;align-items:center}.movies__filters>*{width:50%}.movies__radio{display:inline-block;margin:0 0.5rem}.title--medium{margin:0 0 0.25rem 0;font-size:1.125rem;font-weight:700}.movies__filters{display:flex;align-items:center}.border--medium{color:#cecece;background-color:black}.form__input-text{color:black;background-color:#cecece}

*{margin:0;padding:0;box-sizing:border-box;font-family:'Actor', sans-serif;text-decoration:none}

